nav {
    padding: 0 1.5rem;
}
.content {
    min-height: calc(100vh - 70px - 64px);
    padding: 1.5rem 0;
}
.movies {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
}
.search-btn {
    position: absolute;
    top: 0;
    right: 0.5rem;
}
.radio-filter label:not(:last-child) {
    margin-right: 1rem;
}
